import React, { Fragment, useEffect, useState } from 'react';
import { Table } from './Table';
import Pagination from '../Pagination/Pagination';
import { ReactComponent as ArrowPrev } from './img/arrowPrev.svg';
import { ReactComponent as ArrowNext } from './img/arrowNext.svg';
import { getAllData } from '../api/api';
import { Data } from './Types/Data';
import { v4 as uuidv4 } from 'uuid';

import classNames from 'classnames';

import './styles/Info.scss';

type Props = {
  isImageView: boolean,
  isCategoryOpen: boolean,
  filtersSetting: any,
  settingFields: boolean,
  data: Data[],
  handleOnSort: (type: string, fieldName: string) => void,
  headers: string[]
};

export const Info: React.FC<Props> = ({
  isImageView,
  isCategoryOpen,
  filtersSetting,
  settingFields,
  data,
  handleOnSort,
  headers
}) => {
  const [perPage] = useState(50);
  const [currentPage, setCurrentPage] = useState(1);
  const [currentData, setFilteredData] = useState<Data[]>(data);
  const [fillter, setFillter] = useState(null);
  const [isVisiable, setIsVisiable] = useState(true);

  const newItems: any[] = [];

  const handleOnCheckbox = (item: Data) => {
    if (newItems.find(newItem => newItem === item)) {
      const index = newItems.findIndex(newItem => newItem === item);
      newItems.splice(index, 1);

      console.log(newItems);
      return;
    }

    newItems.push(item);
    console.log(newItems);
  }

  console.log(newItems);

  const amountOfPages = Math.ceil(data.length / perPage);

  const startIndex = (currentPage - 1) * perPage;
  const endIndex = currentPage * perPage < data.length
    ? currentPage * perPage
    : data.length;

  const foundItems = data.slice(startIndex, endIndex);

  useEffect(() => {
    setFilteredData(getAllData(filtersSetting))
  }, [fillter]);

  const handleOnPageChange = (pageNumber: number) => {
    setCurrentPage(pageNumber);
  };

  const handleOnVisiable = () => {
    setIsVisiable(!isVisiable);
  }

  return (
    <>
      <div className="fields__count">
        <span className='fields__count--green counter'>Общее количество: <span>234 поз.</span></span>
        <span className='fields__count--red counter'>Продавались за последние 7 дней: <span>16 поз.</span></span>
        <span className='fields__count--purple counter'>Товаров без сделок: <span>749 поз.</span></span>
      </div>
      <Table
        total={data}
        data={foundItems}
        currentPage={currentPage}
        onPageChange={handleOnPageChange}
        isImageView={isImageView}
        settingFields={settingFields}
        handleOnSort={handleOnSort}
        tableHeaders={headers}
        handleOnCheckbox={handleOnCheckbox}
        newItems={newItems}
      />
      <div className="fields__pagination pagination">
        <span>
          <span>
            <ArrowPrev
              onClick={() => {
                if (currentPage === 1) return;
                setCurrentPage(currentPage - 1)
              }}
            />
          </span>
          {currentPage === 1 ? (
            <div
              onClick={() => {
                if (currentPage === 1) return;
                setCurrentPage(currentPage - 1)
              }}
              className={
                classNames(
                  'pagination__word-prev',
                  'pagination__word-prev--disabled'
                )}
            >
              Пред
            </div>
          ) : (
            <span
              onClick={() => {
                if (currentPage === 1) return;
                setCurrentPage(currentPage - 1)
              }}
              className={
                classNames(
                  'pagination__word-prev',
                  'pagination-item'
                )}
            >
              Пред
            </span>
          )}
          {/* {data && (
            data.map((_: any, index: number) => {
              console.log(index);

              if (index === 8) {
                return (
                  <Fragment key={uuidv4()}>
                    <span className='pagination__num pagination__num'>...</span>
                    <span onClick={(e) => {
                      handleOnPageChange(+e.currentTarget.innerText)
                    }} className='pagination__num pagination__num--active'>{index - 2}</span>
                    <span className='pagination__num pagination__num--active'>{index - 1}</span>
                    <span className='pagination__num pagination__num--active'>{index}</span>
                  </Fragment>
                )
              }

              if (index > 7) {
                return;
              }

              return (
                <span
                  onClick={(e) => {
                    handleOnPageChange(+e.currentTarget.innerText)
                  }}
                  key={uuidv4()}
                  className={classNames(
                    'pagination__num',
                    { 'pagination__num--active': currentPage === index + 1 }
                  )}
                >
                  {index + 1}
                </span>
              )
            })
          )} */}
          <Pagination
            className="pagination-bar"
            currentPage={currentPage}
            totalCount={data.length}
            pageSize={perPage}
            onPageChange={(page: any) => setCurrentPage(page)}
          />
          {currentPage === amountOfPages ? (
            <div className='pagination__word-next pagination__word-prev--disabled'
              onClick={() => {
                if (currentPage === amountOfPages) return;
                setCurrentPage(currentPage + 1)
              }}
            >
              След
            </div>
          ) : (
            <span className='pagination__word-next pagination-item'
              onClick={() => {
                if (currentPage === amountOfPages) return;
                setCurrentPage(currentPage + 1)
              }}
            >
              След
            </span>
          )}
          <span>
            <ArrowNext
              className='n'
              onClick={() => {
                if (currentPage === amountOfPages) return;
                setCurrentPage(currentPage + 1)
              }}
            />
          </span>
        </span>
      </div>
    </>
  );
}
