import { v4 as uuidv4 } from 'uuid';

import { CategoryManagementTree } from '../CategoryManagement/CategoryManagementTree';
import { CategoryManagementTreeNode } from '../CategoryManagement/CategoryManagementTree';

import './styles/NewProduct.scss';
import classNames from 'classnames';

type DropDownProps = {
  options: any[]
}

const DropDown: React.FC<DropDownProps> = ({ options }) => {

  return (
    <>
      <div className="new-product__dropdown-menu">
        {options.map((option: any, i) => (
          <>

            <div className='new-product__dropdown-items'>
              <div
                className="new-product__dropdown-item"
                key={uuidv4()}
              >
                {option}
              </div>
            </div>

          </>
        ))}
      </div>
    </>
  );
}

type Props = {
  data: any[]
}

