export function getAllData( {name, status}: any = false) {


  let filteredData = data;

  if (name) {
    filteredData = data.filter(item => item.name.toLowerCase().includes(name.toLowerCase()));
  }

  // if (englishName) {
  //   filteredData = data.filter(item => item.name.toLowerCase().includes(name.toLowerCase()));
  // }

  // if (status) {
  //   filteredData = data.filter(item => item.name.includes(name));
  // }

  // if (description) {
  //   filteredData = data.filter(item => item.name.includes(name));
  // }

  // if (category) {
  //   filteredData = data.filter(item => item.category.includes(category));
  // }

  return filteredData;
};

